<!-- eslint-disable object-curly-newline -->
<!-- eslint-disable no-undef -->
<script setup>
import {
  GoogleMap,
  Marker,
  MarkerCluster,
  CustomControl,
  InfoWindow
} from "vue3-google-map";
import { ref, onMounted, watch, nextTick } from "vue";
import { useStore } from "vuex";
import { useThrottleFn } from "@vueuse/core";
import personIcon from "@/assets/logo.png";

const store = useStore();
const mapRef = ref(null);
const myPosition = ref({ lat: 0, lng: 0 });
const latitude = ref();
const longitude = ref();
const searchInput = ref({ position: "TOP_CENTER", margin: "mt-3" });
const placesService = ref(null);

async function initPlacesService() {
  const { PlacesService } = await google.maps.importLibrary("places");
  placesService.value = new PlacesService(mapRef.value.map);
}

function geoSuccess(position) {
  latitude.value = position.coords.latitude;
  longitude.value = position.coords.longitude;
  myPosition.value = { lat: latitude.value, lng: longitude.value };
}
function geoError() {
  store.dispatch("alertModules/updateMessage", {
    message: "無法取得定位",
    status: false
  });
}
function geoFindMe() {
  if ("geolocation" in navigator) {
    navigator.geolocation.getCurrentPosition(geoSuccess, geoError);
  } else {
    store.dispatch("alertModules/updateMessage", {
      message: "無法取得定位",
      status: false
    });
  }
}

const myAddress = ref("");
const showMyPosition = ref(false);
const myIcon = personIcon;
function findMe() {
  showMyPosition.value = true;
  geoFindMe();
  mapRef.value.map.setCenter(myPosition.value);
  mapRef.value.map.setZoom(17);

  const geocoder = new google.maps.Geocoder();
  const position = myPosition.value;

  geocoder.geocode({ location: position }, (results, status) => {
    if (status === "OK" && results[0]) {
      myAddress.value = results[0].formatted_address;
    }
  });
}

watch(
  () => mapRef.value?.ready,
  ready => {
    if (!ready) return;
    initPlacesService();
    geoFindMe();
  }
);

function viewControl() {
  if (window.innerWidth < 992) {
    searchInput.value = { position: "BOTTOM_CENTER", margin: "mb-5" };
  } else {
    searchInput.value = { position: "TOP_CENTER", margin: "mt-3" };
  }
}
viewControl();

onMounted(() => {
  window.addEventListener("resize", viewControl);
});

const searchValue = ref("");
const searchResult = ref([]);

function findSearchCenterPosition(results) {
  let centerPosition = null;
  let maxCount = 0;

  results.forEach(place => {
    const count = place.user_ratings_total || 0;
    if (count > maxCount) {
      maxCount = count;
      centerPosition = place;
    }
  });

  if (centerPosition) {
    const position = centerPosition.geometry.location;

    mapRef.value.map.setCenter(position);
    mapRef.value.map.setZoom(17);
  }
}
async function getDetailsArr(resultArr) {
  const detailsPromises = [];

  if (resultArr) {
    resultArr.forEach(item => {
      const request = {
        placeId: item.place_id,
        language: "zh-TW"
      };

      const detailPromise = new Promise((resolve, reject) => {
        placesService.value.getDetails(request, (result, status) => {
          if (status === "OK") {
            resolve(result);
          } else {
            reject(status);
          }
        });
      });

      detailsPromises.push(detailPromise);
    });
  }

  try {
    const results = await Promise.all(detailsPromises);
    searchResult.value = results;
    nextTick(() => {
      findSearchCenterPosition(results);
    });
  } catch (error) {
    store.dispatch("alertModules/updateMessage", {
      message: error,
      status: false
    });
  }
}
const mapSearch = useThrottleFn(() => {
  if (!searchValue.value) {
    store.dispatch("alertModules/updateMessage", {
      message: "請輸入關鍵字搜尋",
      status: false
    });
    return;
  }
  if (!placesService.value) {
    initPlacesService();
  }
  const request = {
    location: myPosition.value,
    radius: "10",
    query: searchValue.value
  };
  placesService.value.textSearch(request, (results, status) => {
    if (status === "OK") {
      getDetailsArr(results);
    } else {
      store.dispatch("alertModules/updateMessage", {
        message: "搜尋失敗，請調整關鍵字重新搜尋或重新整理",
        status: false
      });
    }
  });
}, 1000);

const infoWindows = ref([]);

const closeInfoWindows = i => {
  infoWindows.value.forEach((dom, index) => {
    if (index !== i) {
      dom.infoWindow.close();
    }
  });
};
const clickMarkerEvent = i => {
  closeInfoWindows(i);
};

function showStreetView(showPosition) {
  const panorama = mapRef.value.map.getStreetView();
  panorama.setPosition(showPosition);
  panorama.setPov({
    heading: 265,
    pitch: 0
  });
  panorama.setVisible(true);
}

function getDirections(destination) {
  return `https://www.google.com/maps/dir/?api=1&destination=${destination}`;
}
</script>

<template>
  <GoogleMap
    api-key="AIzaSyAlcNVRIoNWdMOebpcKt1cyfkhX17E52pM"
    style="width: 100%; height: 1000px"
    :center="myPosition"
    :zoom="15"
    ref="mapRef"
    @click="closeInfoWindows"
    class="box"
  >
    <CustomControl :position="searchInput.position">
      <div class="input-group" :class="searchInput.margin">
        <input
          v-model.trim="searchValue"
          @keyup.enter="mapSearch"
          type="text"
          class="form-control"
          placeholder="請搜尋"
          aria-label="search"
        />
        <button @click="mapSearch" class="btn btn-secondary" type="button">
          搜尋
        </button>
      </div>
    </CustomControl>
    <CustomControl position="RIGHT_BOTTOM">
      <button class="findMe-btn" @click="findMe">
        ME
      </button>
    </CustomControl>

    <MarkerCluster>
      <Marker
        v-for="(item, index) in searchResult"
        :key="index"
        :options="{ position: item.geometry.location, title: item.name }"
        @click="clickMarkerEvent"
      >
        <InfoWindow :ref="el => (infoWindows[index + 1] = el)">
          <div class="mark-info">
            <span class="title">{{ item.name }}</span>
            <div id="bodyContent">
              <p>{{ item.formatted_address }}</p>
              <a :href="item.url" target="_blank">在 Google 地圖上查看</a>
              <div class="d-flex justify-content-end align-items-center mt-1">
                <a
                  v-if="item.international_phone_number"
                  :href="`tel:${item.international_phone_number}`"
                  class="me-auto"
                >
                  撥打電話
                </a>
                <a
                  :href="getDirections(myPosition, item.geometry.location)"
                  target="_blank"
                  class="btn btn-primary me-1 text-white text-decoration-none"
                >
                  導航
                </a>
                <button
                  @click="showStreetView(item.geometry.location)"
                  class="btn btn-warning text-white"
                >
                  街景
                </button>
              </div>
            </div>
          </div>
        </InfoWindow>
      </Marker>
    </MarkerCluster>

    <Marker
      v-if="showMyPosition"
      :options="{
        position: myPosition,
        title: '我的位置',
        icon: {
          url: myIcon
        }
      }"
      @click="clickMarkerEvent"
    >
      <InfoWindow :ref="el => (infoWindows[0] = el)">
        <div class="mark-info">
          <span class="title">我的位置</span>
          <div id="bodyContent" v-if="myAddress">
            <p>{{ myAddress }}</p>
          </div>
          <button
            class="btn btn-danger float-end"
            @click="showMyPosition = false"
          >
            移除標記
          </button>
        </div>
      </InfoWindow>
    </Marker>
  </GoogleMap>
</template>

<style lang="scss">
.box {
  max-height: 90vh;
}
.mark-info {
  width: 180px;
  font-size: 13px;
  .title {
    font-size: 14px;
    font-weight: bold;
  }
  .btn {
    font-size: 12px;
    padding: 3px 5px;
  }
  a {
    color: #1a73e8;
  }
  a:hover {
    text-decoration: underline;
  }
}
.findMe-btn {
  background-color: #fff;
  color: #666;
  border: none;
  outline: none;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  margin-right: 10px;
  padding: 0;
}
.findMe-btn:hover {
  color: #333;
}
</style>
